import React from 'react';

import Menu from '../shared/Menu';
import Footer from '../shared/Footer';
import Servicio from './Servicio';
import Carousel from '../shared/Carousel'

class Servicios extends React.Component {

   render() {
      const items = [
         <Servicio
            key="desarrollo"
            Active="active"
            Title="Desarrollo de aplicaciones"
            Description="Somos un grupo de profesionales con amplia experiencia en el desarrollo
            y gestion de proyectos tecnologicos"
         />,
         <Servicio
            key="consultoria"
            Title="Consultoria"
            Description="Contactanos si necesitas consultoria en alguna de las tecnologias"
         />
      ];

      return (

         <>

            <Menu active="/servicios" />

            {<main role="main" className="container">

               <div className="container">

                  <Carousel
                     id="servicios"
                     items={items}
                  />

               </div>

            </main>}

            <Footer />

         </>

      )

   }

}

export default Servicios;